<template>
  <div class="chart-wrap">
    <h4>山西跃峰电力承装有限公司</h4>
    <div id="main"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  data() {
    return {};
  },
  methods: {
    initChart() {
      var ROOT_PATH =
        "https://cdn.jsdelivr.net/gh/apache/echarts-website@asf-site/examples";

      var chartDom = document.getElementById("main");
      var myChart = echarts.init(chartDom);
      var option;

      const upColor = "#00da3c";
      const downColor = "#ec0000";
      var that = this;
      $.get("/demo/demoChart", function(rawData) {
        let res = JSON.parse(rawData);
        console.log(typeof res);
        var data = that.splitData(res);
        myChart.setOption(
          (option = {
            animation: false,
            legend: {
              bottom: 10,
              left: "center",
              data: [
                "Dow-Jones index",
                "持有量",
                "未成交量",
                "可买量",
                "可卖量"
              ]
            },
            tooltip: {
              trigger: "axis",
              axisPointer: {
                type: "cross"
              },
              borderWidth: 1,
              borderColor: "#ccc",
              padding: 10,
              textStyle: {
                color: "#000"
              },
              position: function(pos, params, el, elRect, size) {
                const obj = {
                  top: 10
                };
                obj[["left", "right"][+(pos[0] < size.viewSize[0] / 2)]] = 30;
                return obj;
              }
              // extraCssText: 'width: 170px'
            },
            axisPointer: {
              link: [
                {
                  xAxisIndex: "all"
                }
              ],
              label: {
                backgroundColor: "#777"
              }
            },
            toolbox: {
              feature: {
                dataZoom: {
                  yAxisIndex: false
                },
                brush: {
                  type: ["lineX", "clear"]
                }
              }
            },
            brush: {
              xAxisIndex: "all",
              brushLink: "all",
              outOfBrush: {
                colorAlpha: 0.1
              }
            },
            visualMap: {
              show: false,
              seriesIndex: 5,
              dimension: 2,
              pieces: [
                {
                  value: 1,
                  color: downColor
                },
                {
                  value: -1,
                  color: upColor
                }
              ]
            },
            grid: [
              {
                left: "10%",
                right: "8%",
                height: "50%"
              },
              {
                left: "10%",
                right: "8%",
                top: "63%",
                height: "16%"
              }
            ],
            xAxis: [
              {
                type: "category",
                data: data.categoryData,
                scale: true,
                boundaryGap: false,
                axisLine: { onZero: false },
                splitLine: { show: false },
                min: "dataMin",
                max: "dataMax",
                axisPointer: {
                  z: 100
                }
              },
              {
                type: "category",
                gridIndex: 1,
                data: data.categoryData,
                scale: true,
                boundaryGap: false,
                axisLine: { onZero: false },
                axisTick: { show: false },
                splitLine: { show: false },
                axisLabel: { show: false },
                min: "dataMin",
                max: "dataMax"
              }
            ],
            yAxis: [
              {
                scale: true,
                splitArea: {
                  show: true
                }
              },
              {
                scale: true,
                gridIndex: 1,
                splitNumber: 2,
                axisLabel: { show: false },
                axisLine: { show: false },
                axisTick: { show: false },
                splitLine: { show: false }
              }
            ],
            //区域缩放
            dataZoom: [
              {
                type: "inside",
                xAxisIndex: [0, 1],
                start: 98,
                end: 100
              },
              {
                show: true,
                xAxisIndex: [0, 1],
                type: "slider",
                top: "85%",
                start: 98,
                end: 100
              }
            ],
            series: [
              {
                name: "Dow-Jones index",
                type: "candlestick",
                data: data.values,
                itemStyle: {
                  color: upColor,
                  color0: downColor,
                  borderColor: undefined,
                  borderColor0: undefined
                },
                tooltip: {
                  formatter: function(param) {
                    param = param[0];
                    return [
                      "Date: " +
                        param.name +
                        '<hr size=1 style="margin: 3px 0">',
                      "Open: " + param.data[0] + "<br/>",
                      "Close: " + param.data[1] + "<br/>",
                      "Lowest: " + param.data[2] + "<br/>",
                      "Highest: " + param.data[3] + "<br/>"
                    ].join("");
                  }
                }
              },
              {
                name: "持有量",
                type: "line",
                data: that.calculateMA(5, data),
                smooth: true,
                lineStyle: {
                  opacity: 0.5
                }
              },
              {
                name: "未成交量",
                type: "line",
                data: that.calculateMA(10, data),
                smooth: true,
                lineStyle: {
                  opacity: 0.5
                }
              },
              {
                name: "可买量",
                type: "line",
                data: that.calculateMA(20, data),
                smooth: true,
                lineStyle: {
                  opacity: 0.5
                }
              },
              {
                name: "可卖量",
                type: "line",
                data: that.calculateMA(30, data),
                smooth: true,
                lineStyle: {
                  opacity: 0.5
                }
              }
            ]
          }),
          true
        );
        //特定位置高亮或者显示提示框
        myChart.dispatchAction({
          type: "brush",
          areas: [
            {
              brushType: "lineX",
              coordRange: ["00:00-01:00", "11:00-12:00"],
              xAxisIndex: 0
            }
          ]
        });
      });
    },
    splitData(rawData) {
      let categoryData = [];
      let values = [];
      let volumes = [];
      for (let i = 0; i < rawData.length; i++) {
        categoryData.push(rawData[i].splice(0, 1)[0]);
        values.push(rawData[i]);
        volumes.push([
          i,
          rawData[i][4],
          rawData[i][0] > rawData[i][1] ? 1 : -1
        ]);
      }
      return {
        categoryData: categoryData,
        values: values,
        volumes: volumes
      };
    },
    //计算日均值
    calculateMA(dayCount, data) {
      var result = [];
      for (var i = 0, len = data.values.length; i < len; i++) {
        if (i < dayCount) {
          result.push("-");
          continue;
        }
        var sum = 0;
        for (var j = 0; j < dayCount; j++) {
          sum += data.values[i - j][1];
        }
        result.push(+(sum / dayCount).toFixed(3));
      }
      return result;
    }
  },
  created() {},
  mounted() {
    this.initChart();
  }
};
</script>
<style scoped>
#main {
  width: 1000px;
  height: 600px;
}
</style>



